import React from 'react'
import Layout from 'components/Layout'
import GatheringLink from 'components/GatheringLink'

export default function GatheringsIndexTemplate({ intl, gatherings }) {
  const UpcomingGatherings = gatherings
    .filter(gathering => !!gathering.node.frontmatter.date)
    .filter(gathering => gathering.node.frontmatter.addToIndex)
    .filter(gathering => new Date() < Date.parse(gathering.node.frontmatter.date))
    .map(gathering => <li key={gathering.node.id}><GatheringLink key={gathering.node.id} gathering={gathering.node} /></li>)

  const PreviousGatherings = gatherings
    .filter(gathering => !!gathering.node.frontmatter.date)
    .filter(gathering => gathering.node.frontmatter.addToIndex)
    .filter(gathering => new Date() > Date.parse(gathering.node.frontmatter.date))
    .map(gathering => <li key={gathering.node.id}><GatheringLink key={gathering.node.id} gathering={gathering.node} /></li>)

  return (
    <Layout fullMenu>
      <article id="main">
        <header>
          <h2>{intl['gatherings']}</h2>
        </header>
        <section className="wrapper style5">
          <div className="inner">
            <h3>{intl['upcoming_gatherings']}</h3>
            <ul>
              {UpcomingGatherings}
            </ul>

            <hr/>

            <h3>{intl['previous_gatherings']}</h3>
            <ul>
              {PreviousGatherings}
            </ul>
          </div>
        </section>
      </article>
    </Layout>
  )
}
