import React from 'react'
import GatheringsIndexTemplate from 'intl/GatheringsIndexTemplate'
import { graphql } from 'gatsby'

const lang = {
  'gatherings': 'Gatherings',
  'upcoming_gatherings': 'Upcoming Gatherings',
  'previous_gatherings': 'Previous Gatherings'
}

const GatheringPage = ({ data }) => (
  <GatheringsIndexTemplate intl={lang} gatherings={data.allMarkdownRemark.edges}/>
)

export default GatheringPage

export const pageQuery = graphql`
  query {
    allMarkdownRemark(
      sort: { order: ASC, fields: [ frontmatter___date ] },
      filter: { frontmatter: { lang: { eq: "en" }, type: { eq: "gathering" } } }
    ) {
      edges {
        node {
          id
          frontmatter {
            path
            date(formatString: "MMMM DD, YYYY")
            title
            addToIndex
          }
        }
      }
    }
  }
`
